import React, { Component } from 'react';
import './ExperienceCard.css';
import { Fade } from "react-reveal";

class ExperienceCard extends Component {
  formatDuration = (durationString) => {
    // Handle various date formats like "June 2020 - PRESENT", "May 2018 - Oct 2018", etc.
    const parts = durationString.split(' - ');
    if (parts.length === 2) {
      const start = this.formatDatePart(parts[0]);
      const end = parts[1].toUpperCase() === 'PRESENT' ? 'PRESENT' : this.formatDatePart(parts[1]);
      return `${start} - ${end}`;
    }
    return durationString;
  };

  formatDatePart = (datePart) => {
    const monthNames = {
      'January': 'JAN', 'February': 'FEB', 'March': 'MAR', 'April': 'APR',
      'May': 'MAY', 'June': 'JUN', 'July': 'JUL', 'August': 'AUG',
      'September': 'SEP', 'October': 'OCT', 'November': 'NOV', 'December': 'DEC',
      'Jan': 'JAN', 'Feb': 'FEB', 'Mar': 'MAR', 'Apr': 'APR',
      'Jun': 'JUN', 'Jul': 'JUL', 'Aug': 'AUG', 'Sep': 'SEP',
      'Oct': 'OCT', 'Nov': 'NOV', 'Dec': 'DEC'
    };
    
    const words = datePart.trim().split(' ');
    if (words.length === 2) {
      const month = monthNames[words[0]] || words[0].toUpperCase();
      const year = words[1];
      return `${month} - ${year}`;
    }
    return datePart;
  };

  render() {
    const experience = this.props.experience;
    const theme = this.props.theme;
    
    return (
      <div className="timeline-item">
        <div 
          className="timeline-date-left" 
          style={{ color: '#1e3a8a' }}
        >
          {this.formatDuration(experience["duration"])}
        </div>
        
        <div 
          className="timeline-marker" 
          style={{ backgroundColor: experience.color || theme.highlight }}
        />
        
        <div className="timeline-content" style={{ background: theme.body, borderColor: theme.headerColor }}>
          <Fade bottom duration={1000} distance="20px">
            <div className="timeline-card-header">
              <div className="timeline-logo">
                <img
                  src={require(`../../assests/images/${experience["logo_path"]}`)}
                  alt={experience.company}
                  className="timeline-logo-img"
                />
              </div>
              <div className="timeline-details">
                <h3 className="timeline-title" style={{ color: theme.text }}>
                  {experience["title"]}
                </h3>
                <p className="timeline-company" style={{ color: theme.text }}>
                  <a href={experience["company_url"]} target="_blank" rel="noopener noreferrer">
                    {experience["company"]}
                  </a>
                </p>
                <div className="timeline-location" style={{ color: theme.secondaryText }}>
                   {experience["location"]}
                </div>
              </div>
            </div>
            <div className="timeline-description" style={{ color: theme.text }}>
              {experience["description"]}
            </div>
          </Fade>
        </div>
      </div>
    );
  }
}

export default ExperienceCard;
