import React, { Component } from "react";
import "./NewsAdmin.css";
import AdminLogin from "./AdminLogin";

class NewsAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isAuthenticated: false,
      currentUser: null,
      allNews: [],
      loading: true,
      editingNews: null,
      creatingNews: false,
      editForm: {
        title: '',
        description: '',
        category: '',
        image: '',
        link: ''
      },
      showCreateIssueModal: false,
      generatedIssueText: '',
      modalType: 'edit' // 'edit' or 'create'
    };
  }

  componentDidMount() {
    console.log('NewsAdmin component mounted');
    console.log('Current URL:', window.location.href);
    console.log('Current pathname:', window.location.pathname);
    this.checkAuthentication();
    this.loadNews();
  }

  checkAuthentication = () => {
    const authToken = localStorage.getItem('newsAdminToken');
    const authExpiry = localStorage.getItem('newsAdminExpiry');
    const currentUser = localStorage.getItem('newsAdminUser');
    
    if (authToken && authExpiry && new Date().getTime() < parseInt(authExpiry)) {
      this.setState({ 
        isAuthenticated: true, 
        currentUser: currentUser 
      });
    } else {
      // Clear expired credentials
      localStorage.removeItem('newsAdminToken');
      localStorage.removeItem('newsAdminExpiry');
      localStorage.removeItem('newsAdminUser');
    }
  };

  handleAuthentication = () => {
    const currentUser = localStorage.getItem('newsAdminUser');
    this.setState({ 
      isAuthenticated: true, 
      currentUser: currentUser 
    });
  };

  handleLogout = () => {
    localStorage.removeItem('newsAdminToken');
    localStorage.removeItem('newsAdminExpiry');
    localStorage.removeItem('newsAdminUser');
    this.setState({ 
      isAuthenticated: false, 
      currentUser: null,
      editingNews: null,
      creatingNews: false 
    });
  };

  loadNews = async () => {
    try {
      const response = await fetch('/news.json?t=' + Date.now());
      if (response.ok) {
        const data = await response.json();
        this.setState({ allNews: data.news || [], loading: false });
      }
    } catch (error) {
      console.error('Error loading news:', error);
      this.setState({ loading: false });
    }
  };

  handleEdit = (news) => {
    this.setState({
      editingNews: news,
      creatingNews: false,
      editForm: {
        title: news.title,
        description: news.description || news.content || '',
        category: news.category || '',
        image: news.image || '',
        link: news.link || ''
      }
    });
  };

  handleCreateNew = () => {
    this.setState({
      creatingNews: true,
      editingNews: null,
      editForm: {
        title: '',
        description: '',
        category: 'General',
        image: '',
        link: ''
      }
    });
  };

  handleBack = () => {
    this.setState({
      editingNews: null,
      creatingNews: false
    });
  };

  handleFormChange = (field, value) => {
    this.setState({
      editForm: {
        ...this.state.editForm,
        [field]: value
      }
    });
  };

  generateEditIssue = () => {
    const { editingNews, editForm } = this.state;
    
    let issueBody = `Edit Target: ${editingNews.title}\n`;
    
    if (editForm.title !== editingNews.title) {
      issueBody += `New Title: ${editForm.title}\n`;
    }
    if (editForm.category !== (editingNews.category || '')) {
      issueBody += `New Category: ${editForm.category}\n`;
    }
    if (editForm.image !== (editingNews.image || '')) {
      issueBody += `New Image: ${editForm.image}\n`;
    }
    if (editForm.link !== (editingNews.link || '')) {
      issueBody += `New Link: ${editForm.link}\n`;
    }
    const originalDesc = editingNews.description || editingNews.content || '';
    if (editForm.description !== originalDesc) {
      issueBody += `\nNew Description:\n${editForm.description}`;
    }

    this.setState({
      generatedIssueText: issueBody,
      showCreateIssueModal: true,
      modalType: 'edit'
    });
  };

  generateCreateIssue = () => {
    const { editForm } = this.state;
    
    let issueBody = `Category: ${editForm.category}\n`;
    if (editForm.image.trim()) {
      issueBody += `Image: ${editForm.image}\n`;
    }
    if (editForm.link.trim()) {
      issueBody += `Link: ${editForm.link}\n`;
    }
    issueBody += `\n${editForm.description}`;

    this.setState({
      generatedIssueText: issueBody,
      showCreateIssueModal: true,
      modalType: 'create'
    });
  };

  copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {
      alert('Copied to clipboard! Now go to GitHub and create a new issue.');
    });
  };

  render() {
    const { 
      isAuthenticated, 
      currentUser, 
      allNews, 
      loading, 
      editingNews, 
      creatingNews,
      editForm, 
      showCreateIssueModal, 
      generatedIssueText,
      modalType 
    } = this.state;

    // Show login if not authenticated
    if (!isAuthenticated) {
      return <AdminLogin onAuthenticated={this.handleAuthentication} />;
    }

    if (loading) {
      return <div className="news-admin-loading">Loading news admin...</div>;
    }

    return (
      <div className="news-admin-container">
        <div className="news-admin-header">
          <div className="header-content">
            <div className="header-title">
              <h1> News Admin Panel</h1>
              <p>Welcome back, {currentUser}!</p>
            </div>
            <div className="header-actions">
              <span className="user-info">Logged in as <strong>{currentUser}</strong></span>
              <button className="logout-btn" onClick={this.handleLogout}>
                <i className="fas fa-sign-out-alt"></i> Logout
              </button>
            </div>
          </div>
        </div>

        {!editingNews && !creatingNews ? (
          <div className="news-list">
            <div className="list-header">
              <h2>All News ({allNews.length})</h2>
              <button className="create-btn" onClick={this.handleCreateNew}>
                <i className="fas fa-plus"></i> Create New News
              </button>
            </div>
            <div className="news-table">
              {allNews.map((news, index) => (
                <div key={news.id || index} className="news-row">
                  <div className="news-info">
                    <div className="news-title">{news.title}</div>
                    <div className="news-meta">
                      <span className="news-date">{news.date}</span>
                      <span className="news-category">{news.category}</span>
                      <span className="news-source">{news.source}</span>
                    </div>
                    <div className="news-description-preview">
                      {news.description.substring(0, 100)}...
                    </div>
                  </div>
                  <div className="news-actions">
                    <button 
                      className="edit-btn"
                      onClick={() => this.handleEdit(news)}
                    >
                       Edit
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="news-edit-form">
            <div className="form-header">
              <h2>{creatingNews ? ' Create News' : ' Edit News'}</h2>
              <button 
                className="back-btn"
                onClick={this.handleBack}
              >
                 Back to List
              </button>
            </div>

            <div className="form-group">
              <label>Title:</label>
              <input
                type="text"
                value={editForm.title}
                onChange={(e) => this.handleFormChange('title', e.target.value)}
                className="form-input"
              />
            </div>

            <div className="form-group">
              <label>Category:</label>
              <select
                value={editForm.category}
                onChange={(e) => this.handleFormChange('category', e.target.value)}
                className="form-select"
              >
                <option value="">Select Category</option>
                <option value="Research">Research</option>
                <option value="Publication">Publication</option>
                <option value="Conference">Conference</option>
                <option value="Achievement">Achievement</option>
                <option value="Event">Event</option>
                <option value="General">General</option>
              </select>
            </div>

            <div className="form-group">
              <label>Image:</label>
              <input
                type="text"
                value={editForm.image}
                onChange={(e) => this.handleFormChange('image', e.target.value)}
                placeholder="image-name.jpg"
                className="form-input"
              />
            </div>

            <div className="form-group">
              <label>Link:</label>
              <input
                type="text"
                value={editForm.link}
                onChange={(e) => this.handleFormChange('link', e.target.value)}
                placeholder="https://example.com"
                className="form-input"
              />
            </div>

            <div className="form-group">
              <label>Description:</label>
              <textarea
                value={editForm.description}
                onChange={(e) => this.handleFormChange('description', e.target.value)}
                className="form-textarea"
                rows="10"
              />
            </div>

            <div className="form-actions">
              <button 
                className="generate-btn"
                onClick={creatingNews ? this.generateCreateIssue : this.generateEditIssue}
              >
                 {creatingNews ? 'Create News Issue' : 'Generate Edit Issue'}
              </button>
            </div>
          </div>
        )}

        {showCreateIssueModal && (
          <div className="modal-overlay">
            <div className="modal-content">
              <div className="modal-header">
                <h3> {modalType === 'create' ? 'Create New News' : 'Edit Existing News'}</h3>
                <button 
                  className="close-btn"
                  onClick={() => this.setState({ showCreateIssueModal: false })}
                >
                  
                </button>
              </div>
              
              <div className="modal-body">
                <p><strong>Steps:</strong></p>
                <ol>
                  <li>Go to your GitHub repository</li>
                  <li>Click "Issues"  "New issue"</li>
                  <li>Title: <code>{modalType === 'create' ? editForm.title : `Edit: ${editingNews?.title}`}</code></li>
                  <li>Add label: <code>{modalType === 'create' ? 'news' : 'edit-news'}</code></li>
                  <li>Copy the content below into the issue body</li>
                  <li>Submit the issue</li>
                </ol>

                <div className="issue-preview">
                  <pre>{generatedIssueText}</pre>
                </div>

                <div className="modal-actions">
                  <button 
                    className="copy-btn"
                    onClick={() => this.copyToClipboard(generatedIssueText)}
                  >
                     Copy to Clipboard
                  </button>
                  <a 
                    href={modalType === 'create' 
                      ? `https://github.com/getnetdemil/getnetdemil.github.io/issues/new?title=${encodeURIComponent(editForm.title)}&labels=news&body=${encodeURIComponent(generatedIssueText)}`
                      : `https://github.com/getnetdemil/getnetdemil.github.io/issues/new?title=Edit: ${encodeURIComponent(editingNews?.title || '')}&labels=edit-news&body=${encodeURIComponent(generatedIssueText)}`
                    }
                    target="_blank"
                    rel="noopener noreferrer"
                    className="github-btn"
                  >
                     Open GitHub Issue
                  </a>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default NewsAdmin;