import React, { Component } from "react";
import "./DegreeCard.css";
import { Fade } from "react-reveal";

class DegreeCard extends Component {
  formatDuration = (durationString) => {
    // Handle various date formats like "2020 - 2024", "Sep 2019 - May 2021", etc.
    const parts = durationString.split(' - ');
    if (parts.length === 2) {
      const start = this.formatDatePart(parts[0]);
      const end = parts[1].toUpperCase() === 'PRESENT' ? 'PRESENT' : this.formatDatePart(parts[1]);
      return `${start} - ${end}`;
    }
    return durationString;
  };

  formatDatePart = (datePart) => {
    const monthNames = {
      'January': 'JAN', 'February': 'FEB', 'March': 'MAR', 'April': 'APR',
      'May': 'MAY', 'June': 'JUN', 'July': 'JUL', 'August': 'AUG',
      'September': 'SEP', 'October': 'OCT', 'November': 'NOV', 'December': 'DEC',
      'Jan': 'JAN', 'Feb': 'FEB', 'Mar': 'MAR', 'Apr': 'APR',
      'Jun': 'JUN', 'Jul': 'JUL', 'Aug': 'AUG', 'Sep': 'SEP',
      'Oct': 'OCT', 'Nov': 'NOV', 'Dec': 'DEC'
    };
    
    const words = datePart.trim().split(' ');
    if (words.length === 2) {
      const month = monthNames[words[0]] || words[0].toUpperCase();
      const year = words[1];
      return `${month} - ${year}`;
    } else if (words.length === 1 && /^\d{4}$/.test(words[0])) {
      // Handle year-only format (e.g., "2020")
      return words[0];
    }
    return datePart;
  };

  render() {
    const degree = this.props.degree;
    const theme = this.props.theme;
    
    return (
      <div className="education-timeline-item">
        <div 
          className="education-timeline-date-left" 
          style={{ color: '#1e3a8a' }}
        >
          {this.formatDuration(degree.duration)}
        </div>
        
        <div 
          className="education-timeline-marker" 
          style={{ backgroundColor: theme.highlight }}
        />
        
        <div className="education-timeline-content" style={{ background: theme.body, borderColor: theme.headerColor }}>
          <Fade bottom duration={1000} distance="20px">
            <div className="education-card-header">
              <div className="university-info">
                <h2 className="university-title" style={{ color: theme.text }}>{degree.title}</h2>
                <h3 className="degree-subtitle" style={{ color: theme.text }}>{degree.subtitle}</h3>
              </div>
            </div>
            
            <div className="education-card-body">
              <div className="degree-descriptions">
                {degree.descriptions.map((sentence, idx) => (
                  <div key={idx} className="degree-description">
                    <span className="description-bullet"></span>
                    <span className="description-text" style={{ color: theme.text }}>{sentence.replace(' ', '')}</span>
                  </div>
                ))}
              </div>
              
              {degree.website_link && (
                <div className="education-card-footer">
                  <a
                    href={degree.website_link}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="visit-website-btn"
                    style={{ color: theme.highlight, borderColor: theme.highlight }}
                  >
                    Visit Website
                  </a>
                </div>
              )}
            </div>
          </Fade>
        </div>
      </div>
    );
  }
}

export default DegreeCard;
