import React, { Component } from "react";
import "./AdminLogin.css";

class AdminLogin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: '',
      password: '',
      isLoading: false,
      error: '',
      showPassword: false
    };
  }

  componentDidMount() {
    // Check if already authenticated
    const authToken = localStorage.getItem('newsAdminToken');
    const authExpiry = localStorage.getItem('newsAdminExpiry');
    
    if (authToken && authExpiry && new Date().getTime() < parseInt(authExpiry)) {
      this.props.onAuthenticated();
    }
  }

  handleSubmit = async (e) => {
    e.preventDefault();
    this.setState({ isLoading: true, error: '' });

    const { username, password } = this.state;

    // Simple authentication - in production, you'd want to use proper authentication
    // For now, using environment-based credentials that can be set in GitHub Pages
    const validCredentials = [
      { user: 'admin', pass: 'getnet2024!' },
      { user: 'getnet', pass: 'newsadmin123!' },
      // You can add more credentials here
    ];

    // Check credentials
    const isValid = validCredentials.some(
      cred => cred.user === username && cred.pass === password
    );

    if (isValid) {
      // Generate session token
      const token = btoa(JSON.stringify({
        user: username,
        timestamp: new Date().getTime(),
        session: Math.random().toString(36).substring(2)
      }));

      // Set expiry for 24 hours
      const expiry = new Date().getTime() + (24 * 60 * 60 * 1000);
      
      localStorage.setItem('newsAdminToken', token);
      localStorage.setItem('newsAdminExpiry', expiry.toString());
      localStorage.setItem('newsAdminUser', username);

      this.setState({ isLoading: false });
      this.props.onAuthenticated();
    } else {
      this.setState({ 
        isLoading: false, 
        error: 'Invalid credentials. Please try again.',
        password: '' // Clear password on error
      });
    }
  };

  handleInputChange = (field, value) => {
    this.setState({ [field]: value, error: '' });
  };

  togglePasswordVisibility = () => {
    this.setState({ showPassword: !this.state.showPassword });
  };

  render() {
    const { username, password, isLoading, error, showPassword } = this.state;

    return (
      <div className="admin-login-container">
        <div className="login-wrapper">
          <div className="login-header">
            <div className="login-logo"></div>
            <h1>News Admin Panel</h1>
            <p>Please sign in to manage website news</p>
          </div>

          <form onSubmit={this.handleSubmit} className="login-form">
            <div className="form-group">
              <label htmlFor="username">Username</label>
              <input
                id="username"
                type="text"
                value={username}
                onChange={(e) => this.handleInputChange('username', e.target.value)}
                placeholder="Enter your username"
                required
                disabled={isLoading}
                className="form-input"
              />
            </div>

            <div className="form-group">
              <label htmlFor="password">Password</label>
              <div className="password-input-wrapper">
                <input
                  id="password"
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => this.handleInputChange('password', e.target.value)}
                  placeholder="Enter your password"
                  required
                  disabled={isLoading}
                  className="form-input"
                />
                <button
                  type="button"
                  className="password-toggle"
                  onClick={this.togglePasswordVisibility}
                  disabled={isLoading}
                >
                  {showPassword ? '' : ''}
                </button>
              </div>
            </div>

            {error && (
              <div className="error-message">
                <i className="fas fa-exclamation-triangle"></i>
                {error}
              </div>
            )}

            <button 
              type="submit" 
              className="login-button"
              disabled={isLoading || !username.trim() || !password.trim()}
            >
              {isLoading ? (
                <>
                  <span className="loading-spinner"></span>
                  Signing in...
                </>
              ) : (
                <>
                  <i className="fas fa-sign-in-alt"></i>
                  Sign In
                </>
              )}
            </button>
          </form>

          <div className="login-footer">
            <div className="login-info">
              <h4> Secure Access</h4>
              <ul>
                <li> Session expires in 24 hours</li>
                <li> Credentials stored locally only</li>
                <li> No external authentication required</li>
              </ul>
            </div>

            <div className="login-help">
              <h4> What You Can Do:</h4>
              <ul>
                <li>Create new news posts</li>
                <li>Edit existing news</li>
                <li>Manage images and links</li>
                <li>Auto-publish via GitHub</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AdminLogin;