import React, { Component } from "react";
import "./NewsContainer.css";
import NewsCard from "../../components/newsCard/NewsCard";
import { newsData } from "../../portfolio.js";
import { Fade } from "react-reveal";

class NewsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allNews: [],
      loading: true,
      error: null,
      newsSource: 'portfolio'
    };
  }

  componentDidMount() {
    this.fetchNews();
  }

  fetchNews = async () => {
    try {
      let allNewsItems = [];
      
      // 1. Try to fetch from GitHub Issues-generated JSON file (primary source)
      try {
        const jsonResponse = await fetch('/news.json?t=' + Date.now()); // Cache bust
        if (jsonResponse.ok) {
          const jsonData = await jsonResponse.json();
          console.log(`Loaded ${jsonData.news?.length || 0} news items from GitHub Issues`);
          allNewsItems.push(...(jsonData.news || []).map(item => ({...item, source: 'github-issues'})));
        }
      } catch (jsonError) {
        console.log('GitHub Issues news not available, trying backup sources');
      }

      // 2. Fallback to backup JSON in src folder
      if (allNewsItems.length === 0) {
        try {
          const backupResponse = await fetch('/src/news-backup.json?t=' + Date.now());
          if (backupResponse.ok) {
            const backupData = await backupResponse.json();
            allNewsItems.push(...(backupData.news || []).map(item => ({...item, source: 'backup'})));
          }
        } catch (backupError) {
          console.log('Backup news not available');
        }
      }
      
      // 3. Final fallback to manual news from portfolio.js
      const portfolioNews = newsData.news || [];
      allNewsItems.push(...portfolioNews.map(item => ({...item, source: 'portfolio'})));
      
      // Remove duplicates and sort by date (newest first)
      const uniqueNews = this.removeDuplicates(allNewsItems);
      const sortedNews = uniqueNews.sort((a, b) => new Date(b.date) - new Date(a.date));
      
      this.setState({
        allNews: sortedNews,
        loading: false,
        newsSource: this.getNewsSourceSummary(allNewsItems)
      });
      
    } catch (error) {
      console.error('Error fetching news:', error);
      this.setState({
        error: error.message,
        loading: false,
        allNews: newsData.news || []
      });
    }
  };

  removeDuplicates = (newsArray) => {
    const seen = new Set();
    return newsArray.filter(item => {
      const key = `${item.title}_${item.date}`;
      if (seen.has(key)) {
        return false;
      }
      seen.add(key);
      return true;
    });
  };

  getNewsSourceSummary = (newsArray) => {
    const sources = newsArray.map(item => item.source);
    const uniqueSources = [...new Set(sources)];
    return uniqueSources.join(', ');
  };

  render() {
    const theme = this.props.theme;
    const { allNews, loading, error } = this.state;

    if (loading) {
      return (
        <div className="news-container">
          <div className="loading-news" style={{ color: theme.text }}>
            Loading latest updates...
          </div>
        </div>
      );
    }

    if (!allNews.length) {
      return (
        <div className="news-container">
          <div className="no-news" style={{ color: theme.text }}>
            No news updates available at the moment.
          </div>
        </div>
      );
    }

    return (
      <div className="news-container">
        
        {error && (
          <div className="news-error" style={{ color: theme.highlight }}>
            Note: Some news sources unavailable. Showing available updates.
          </div>
        )}
        
        <div className="news-cards-div">
          <div className="timeline-container">
            {allNews.map((news, i) => (
              <Fade bottom duration={2000} distance="40px" key={news.id || i}>
                <NewsCard
                  news={news}
                  theme={theme}
                  isTimeline={true}
                />
              </Fade>
            ))}
          </div>
        </div>
      </div>
    );
  }
}

export default NewsContainer;